.App-header {
  background-color: #084bd1;
  min-height: 100vh;
  display: flex;

  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}
.box {
  margin: 20px auto;
  width: 500px;
  background-color: cornflowerblue;
}
.box h4 {
  text-align: center;
  background-color: darkgreen;
}
.App {
  text-align: center;
  margin: auto;
  width: 50%;
}
.myForm {
  width: 100%;
  background-color: darkgrey;
  border-radius: 5px;
  padding: 20px;
}
.formField {
  width: 100%;
  padding: 12px 20px;
  margin: 8px 0;
  display: block;
  border: 1px solid #084bd1;
  border-radius: 5px;
  box-sizing: border-box;
}
.myButton {
  width: 100%;
  background-color: #084bd1;
  color: aliceblue;
  padding: 14px 20px;
  margin: 8px 0;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}
.myListBox {
  width: 100%;
  text-align: center;
  margin: auto;
  padding: 20px;
  border-radius: 5px;
  border: 1px solid #084bd1;
}

.myListButton {
  display: inline-block;
  width: 50%;
  padding: 0 12px;
  margin: 0 12px;
  background-color: blueviolet;
  color: white;
}
table,
th,
td {
  border: 1px solid black;
  border-collapse: collapse;
}
th,
td {
  padding: 15px;
  text-align: left;
}
#t01 {
  width: 100%;
  background-color: #f1f1c1;
}
